import React, { Component } from "react";

export default class ProductRow extends Component {
  render() {
    const product = this.props.filaProducto;
    const nombre = product.stocked ? (
      product.name
    ) : (
      <span style={{ color: "red" }}>{product.name}</span>
    );

    return (
      <tr>
        <td>{nombre}</td>
        <td>{product.price}</td>
      </tr>
    );
  }
}
